/* eslint-disable max-len */
/* eslint-disable require-jsdoc */
import React, {useState} from "react";
import "./summary.css";
import downArrow from "../assets/white-arrow.png";
import orangeLocale from "../assets/orange-locale.png";
import greenLocale from "../assets/green-locale.png";

export default function Summary({
  currentStop,
  nextStop,
  data,
  busStatus,
  lastUpdate,
  currentLocation,
}) {
  const [isCollapsed, setIsCollapsed] = useState(false);
  const toggleSummary = () => setIsCollapsed(!isCollapsed);

  // Provide safe defaults if currentStop/nextStop are null
  const cs = currentStop || {id: null, Address: "N/A"};
  const ns = nextStop || {id: null, Address: "N/A"};

  // Status text
  const currentStatusText = (() => {
    // Bus hasn't started yet
    if (currentStop == null && nextStop == null && busStatus === "idle") {
      return "Bus has not started ";
    }

    // Bus is idle at a stop
    if (busStatus === "idle" && currentStop) {
      return `Bus is at Stop ${currentStop.id + 1}`;
    }

    // Bus in transit
    if (busStatus !== "idle" && nextStop) {
      return `In Transit to Stop ${nextStop.id + 1}`;
    }

    // Default fallback
    return "Status unknown";
  })();


  return (
    <div className={`summary ${isCollapsed ? "collapsed" : ""}`}>
      {/* Toggle Arrow */}
      <div className="summary-toggle" onClick={toggleSummary}>
        <img
          src={downArrow}
          alt="Toggle Arrow"
          className={`toggle-arrow ${isCollapsed ? "rotated" : ""}`}
        />
      </div>

      <div className="summary-content">
        {/* Route Progress Section */}
        <div className="route-progress">
          <div className="route-header">
            <p className="section-title">Route Progress</p>
            <p className="progress-count">
              {cs.id !== null ? cs.id : 0} / {data.length} stops completed
            </p>
          </div>
          <div className="progress-bar">
            <progress
              value={cs.id !== null ? cs.id : 0}
              max={data.length}
            ></progress>
          </div>
          <div className="progress-info">
            <p className="progress-percentage">
              {cs.id !== null ? Math.round((cs.id / data.length) * 100) : 0}%
              complete
            </p>
          </div>
        </div>

        {/* Current & Next Stop Section */}
        <div className="stops-container">
          {/* Current Stop */}
          <div className="summary-section current-stop">
            <div className="stop-icon">
              <img src={orangeLocale} alt="Current Bus" />
              <p className="stop-label">{currentStatusText}</p>
            </div>
            <div className="stop-info">
              <p className="stop-name">
                {cs.id !== null ? "Stop " + (cs.id + 1) : "NA"}
              </p>
              <a
                className="stop-address"
                href={
                  cs.id !== null ?
                    `https://www.google.com/maps/?q=${encodeURIComponent(
                        cs.Address,
                    )}` :
                    "#"
                }
                target="_blank"
                rel="noopener noreferrer"
              >
                {cs.Address}
              </a>
            </div>
          </div>

          {/* Next Stop */}
          <div className="summary-section next-stop">
            <div className="stop-icon">
              <img src={greenLocale} alt="Next Stop" />
              <p className="stop-label">Next Stop</p>
            </div>
            <div className="stop-info">
              <p className="stop-name">
                {ns.id !== null ? "Stop " + (ns.id + 1) : "N/A"}
              </p>
              <a
                className="stop-address"
                href={
                  ns.id !== null ?
                    `https://www.google.com/maps/?q=${encodeURIComponent(
                        ns.Address,
                    )}` :
                    "#"
                }
                target="_blank"
                rel="noopener noreferrer"
              >
                {ns.Address}
              </a>
            </div>
          </div>
        </div>

        <div className="divider" style={{paddingTop: "11px"}}>
          <hr />
        </div>

        <div className="last-updated">
          <p>Last Updated: {lastUpdate || "N/A"}</p>
        </div>
      </div>
    </div>
  );
}
