/* eslint-disable max-len */
/* eslint-disable no-unused-vars */
/* eslint-disable require-jsdoc */
import React, {useEffect, useState} from "react";
import "./App.css";
import Map from "./components/map";
import {Header} from "./components/header";
import Information from "./components/Information";
import Summary from "./components/summary";
import {Helmet} from "react-helmet";
import SnowParticles from "./components/SnowFlakes";
import {database} from "./firebase";
import {ref, runTransaction, onValue} from "firebase/database";

function App() {
  const [busStatus, setBusStatus] = useState("idle");
  const [showInfo, setShowInfo] = useState(false);
  const [stops, setStops] = useState([]);
  const [showAddStop, setShowAddStop] = useState(false);
  const [newStopName, setNewStopName] = useState("");
  const [newStopAddress, setNewStopAddress] = useState("");
  const [currentLocation, setCurrentLocation] = useState({lat: 29.619707, lng: -95.3193855});
  const [destinationStop, setDestinationStop] = useState(null);
  const [nextStop, setNextStop] = useState(null);
  const [currentStop, setCurrentStop] = useState(null);
  const [lastUpdate, setLastUpdate] = useState();

  const toggleInfo = () => setShowInfo((prev) => !prev);

  // Real-time fetch stops & bus info
  useEffect(() => {
    const dbRef = ref(database, "/");

    const unsubscribe = onValue(dbRef, (snapshot) => {
      const data = snapshot.val();
      if (!data) return;

      // Map stops into array
      const stopKeys = Object.keys(data.stops || {});
      const mappedStops = stopKeys.map((key) => ({
        id: Number(key),
        Name: `Stop ${Number(key) + 1}`,
        Address: data.stops[key].address || "",
        Latitude: data.stops[key].latitude,
        Longitude: data.stops[key].longitude,
        families: data.stops[key].families || {},
        startingTime: data.stops[key].starting_time || null,
        arrivedAt: data.stops[key].Arrived || null,
        departedAt: data.stops[key].Departure || null,
      }));

      setStops(mappedStops);

      // Set current and next stop
      const csIndex = data.currentStop ?? -1;
      const nsIndex = csIndex >= 0 && csIndex + 1 < mappedStops.length ? csIndex + 1 : null;

      setCurrentStop(csIndex !== 0 ? mappedStops[csIndex] : null);
      setNextStop(nsIndex !== null ? mappedStops[nsIndex] : null);

      // Update bus info
      setBusStatus(data.status || "idle");
      setLastUpdate(new Date().toLocaleTimeString());

      if (data.busLocation) {
        setCurrentLocation({
          lat: data.busLocation.lat,
          lng: data.busLocation.lng,
        });
      }
    });

    return () => unsubscribe();
  }, []);

  // Get lat/lng from address
  async function getLatLng(address) {
    const url = `https://nominatim.openstreetmap.org/search?format=json&q=${encodeURIComponent(address)}`;
    const res = await fetch(url, {
      headers: {
        "User-Agent": "CarolTracker/1.0 (joshinsam@example.com)",
        "Accept-Language": "en",
      },
    });
    if (!res.ok) return null;

    const data = await res.json();
    if (!data.length) return null;
    return {lat: parseFloat(data[0].lat), lon: parseFloat(data[0].lon)};
  }

  // Save new stop atomically to avoid race conditions
  const saveNewStop = async () => {
    if (!newStopName.trim() || !newStopAddress.trim()) {
      alert("Please fill in all fields.");
      return;
    }

    try {
      const result = await getLatLng(newStopAddress);
      if (!result) {
        alert("Could not find the address. Please try again.");
        return;
      }

      const newStop = {
        Name: newStopName,
        address: newStopAddress,
        latitude: result.lat,
        longitude: result.lon,
        Arrived: null,
        Departure: null,
        starting_time: null,
        families: {},
      };

      // Atomic update using transaction on "stops"
      const stopsRef = ref(database, "/stops");
      await runTransaction(stopsRef, (currentStops) => {
        if (!currentStops) currentStops = {};

        const nextId = Object.keys(currentStops).length;
        currentStops[nextId] = newStop;

        return currentStops;
      });

      setShowAddStop(false);
      setNewStopName("");
      setNewStopAddress("");

      alert("Stop added successfully!");
    } catch (error) {
      console.error(error);
      alert("Error adding stop. Try again.");
    }
  };

  return (
    <div className="App">
      <SnowParticles />
      <header className="App-header">
        <Helmet>
          <meta name="viewport" content="width=device-width, initial-scale=1.0" />
        </Helmet>
        <Header toggleInfo={toggleInfo} />
      </header>

      <main className="main-layout">
        {showAddStop && (
          <div className="add-stop-overlay">

            {/* Snow + modal */}
            <div className="add-stop-modal christmas-modal">
              <SnowParticles /> {/* Snow inside the modal container */}
              <h2 className="christmas-title"> Add New Stop </h2>
              <input
                type="text"
                placeholder="Stop Name"
                value={newStopName}
                onChange={(e) => setNewStopName(e.target.value)}
                className="christmas-input"
              />
              <input
                type="text"
                placeholder="Address"
                value={newStopAddress}
                onChange={(e) => setNewStopAddress(e.target.value)}
                className="christmas-input"
              />
              <div className="add-stop-buttons">
                <button className="save-stop-btn christmas-btn" onClick={saveNewStop}>Save</button>
                <button className="cancel-stop-btn christmas-btn" onClick={() => setShowAddStop(false)}>Cancel</button>
              </div>
            </div>
          </div>
        )}


        <section className="map-column" style={{position: "relative"}}>
          <Map
            data={stops}
            currentLocation={currentLocation}
            destinationStop={nextStop}
            busStatus={busStatus}
          />
          <Summary
            data={stops}
            nextStop={nextStop}
            currentStop={currentStop}
            lastUpdate={lastUpdate}
            currentLocation={currentLocation}
          />
        </section>

        <aside className={`side right ${showInfo ? "open" : ""}`}>
          <Information data={stops} onAddStop={() => setShowAddStop(true)} />
        </aside>
      </main>
    </div>
  );
}

export default App;
