/* eslint-disable max-len */
/* eslint-disable require-jsdoc */

import React from "react";
import "./Information.css";

export default function Information({data, onAddStop}) {
  return (
    <div className="information">

      {/*  Only triggers popup */}
      <button className="add-stop-button" onClick={onAddStop}>
        Add a New Stop
      </button>

      {data.map((item) => (
        <div
          key={item.id}
          className={`info-item ${item.arrivedAt ? "arrived-card" : ""}`}
        >
          <div className="info-left">
            <div
              className={`check-circle ${
                item.arrivedAt ? "arrived-circle" : "number-circle"
              }`}
            >
              {item.arrivedAt ? "" : item.id + 1}
            </div>
          </div>

          <div className="info-body">
            <h3 className="info-title">Stop {item.id + 1}</h3>

            <div className="info-line">
              <span className="icon"></span>
              <a
                className="muted"
                href={`https://www.google.com/maps/?q=${encodeURIComponent(
                    item.Address,
                )}`}
                target="_blank"
                rel="noopener noreferrer"
              >
                {item.Address}
              </a>
            </div>

            {item.startingTime && (
              <div className="info-line">
                <span className="icon"></span>
                <span className="muted">Start: {item.startingTime}</span>
              </div>
            )}

            <div className="info-line">
              <span className="icon"></span>
              <span className="muted">
                {item.arrivedAt ?
                  `Arrived: ${item.arrivedAt}${
                      item.departedAt ?
                        ` | Departed: ${item.departedAt}` :
                        ""
                  }` :
                  "Not arrived yet"}
              </span>
            </div>

            {/* need to add a line in between stops whether the bus hasnt moved from one stop after arriving one stop */}

            {item.notes && (
              <div className="note">
                 <em>{item.notes}</em>
              </div>
            )}

            {item.tag && <div className="tag">{item.tag}</div>}
          </div>
        </div>
      ))}
    </div>
  );
}
